import { Mark } from '@ali/4ever-cangjie';

interface Italic extends Mark {
  type: 'italic';
};

const Italic = {
  TYPE: 'italic',

  create(): Italic {
    return Mark.create({ type: Italic.TYPE }) as Italic;
  },

  isItalic(maybeItalic: any): maybeItalic is Italic {
    return Mark.isMark(maybeItalic) && maybeItalic.type === Italic.TYPE;
  },
};

export default Italic;
